@function imgPathBuild($arr) {
  $imgBasePath: '../images/';
  $result: '';
  @for $i from 1 through length($arr) {
    $result: $result + 'url("' + $imgBasePath + nth($arr, $i) + '") '; }

  @return $result; }

html {
  min-height: 100vh;
  background: {
    image: url('../images/base-bg.png');
    repeat: repeat;
    position: center center; } }

body {
  $preloadImg: ('answer-bg.png', 'buddha.png', 'main-bg.png', 'signing.png', 'sprites-signing.png', 'sign.png');

  min-height: 100vh;
  font-size: 4vw;
  background: {
    image: url('../images/main-bg.png');
    color: transparent;
    repeat: no-repeat;
    position: center top;
    size: 200%; }

  &::after {
    position: absolute;
    width: 0;
    height: 0;
    overflow: hidden;
    z-index: -1;
    content: #{imgPathBuild($preloadImg)}; } }

.pageTitle {
  filter: drop-shadow(0 0 10px rgba(yellow, .6));
  margin-left: 15px;
  margin-right: 15px; }

.wrap {
  height: 70vh;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  visibility: hidden; }

.start-box {
  flex: 1; }

.start-link {
  cursor: pointer; }

.copyright {
  text-align: center; }

// compoment
@keyframes signPlay {
  0% {
    background-position: -737px -2px; }

  10% {
    background-position: 2px -2px; }

  100% {
    background-position: 2px -2px; } }

@keyframes handClick {
  0% {
    transform: scale(1.3); }

  10% {
    transform: scale(1); }

  100% {
    transform: scale(1); } }

.signing {
  width: 145px;
  height: 300px;
  display: inline-block;
  overflow: hidden;
  background: url('../images/sprites-signing.png') no-repeat 2px -2px;
  filter: drop-shadow( 2px 4px 5px rgba(#000, .5));

  &.active {
    animation: signPlay 3s 1s steps(5) infinite; } }

.signStart {
  box-shadow: 2px 2px 5px -2px rgba(#333, .65);
  font-size: inherit;

  &::before {
    left: 85% !important; }

  .fa-hand-pointer {
    transform: scale(1.3);
    animation: handClick 3s ease-in .5s infinite; } }

.countdown {
  display: none; }

.answer-box {
  $bdWidth: 32, 33, 55, 38;

  display: none;
  overflow: hidden;
  background-color: transparent;
  padding: 0;
  width: 100%;
  border-color: transparent;
  border-style: solid;
  border-width: #{nth($bdWidth, 1)}px #{nth($bdWidth, 2)}px #{nth($bdWidth, 3)}px #{nth($bdWidth, 4)}px;
  border-image: {
    source: url('../images/answer-bg.png');
    slice: #{nth($bdWidth, 1)} #{nth($bdWidth, 2)} #{nth($bdWidth, 3)} #{nth($bdWidth, 4)};
    width: #{nth($bdWidth, 1)}px #{nth($bdWidth, 2)}px #{nth($bdWidth, 3)}px #{nth($bdWidth, 4)}px;
    repeat: round; }

  .btn-success {
    font-size: inherit; } }

.answer-sub {
  position: relative;
  padding: 20px 15px 20px 10px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  background-color: #fcecd3; }

.answer-title {
  line-height: 1;
  font-size: 1.3em;
  margin-bottom: .5em;
  display: flex;
  align-items: center;
  white-space: nowrap;

  .fortune-value {
    font-size: 1.7em;
    font-weight: bold; }

  &::before {
    content: '';
    display: inline-flex;
    flex: 0 0 (80px / 4);
    margin-right: .2em;
    background: {
      image: url('../images/sign.png');
      repeat: no-repeat;
      position: center center;
      size: 100% 100%; }
    width: (80px / 4);
    height: (250px / 4); } }

.answer-desc {
  font-size: 4.5w; }

.answer-mv {
  position: relative;
  width: 100%;
  padding-top: percentage(9 / 16);
  margin-top: .5em;
  margin-bottom: 2em;

  iframe {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%; } }

@keyframes subscribeNote {
  from {
    top: 0; }

  15% {
    top: -5px; }

  40% {
    top: 0; }

  to {
    top: 0; } }

.subscribe-note {
  font-size: inherit;

  .fa-arrow-up {
    position: relative;
    margin-left: 0.2em;
    top: 0;
    animation: subscribeNote 2.5s ease-out infinite; } }

@keyframes ripple {
  from {
    transform: translate(-50%, -50%) scale(0);
    background-color: rgba(#000, .25); }

  33% {
    transform: translate(-50%, -50%) scale(1);
    background-color: transparent; }

  to {
    transform: translate(-50%, -50%) scale(1);
    background-color: transparent; } }

.btn {
  &.ripple {
    overflow: hidden;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      width: 100%;
      padding-top: 100%;
      background-color: transparent;
      border-radius: 50%;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      animation: ripple 3s ease-out 1s infinite; }

    &:disabled {
      &::before {
        animation-iteration-count: 1; } } } }

// fancybox
.fancybox-is-open .fancybox-bg {
  opacity: .6; }

.fancybox-close-small {
  display: none; }

// loader
.loader-box {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: linear-gradient(-145deg, #f2e837, lighten(#cc2f24, 30%));
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center; }

.loader-sub {
  position: relative;

  .lds-ripple {
    display: block;
    margin: auto;
    left: 5%; } }

.lds-ripple {
  display: inline-block;
  position: relative;
  width: 64px;
  height: 64px; }

.lds-ripple div {
  position: absolute;
  border: 4px solid #fff;
  opacity: 1;
  border-radius: 50%;
  animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite; }

.lds-ripple div:nth-child(2) {
  animation-delay: -0.5s; }

@keyframes lds-ripple {
  0% {
    top: 28px;
    left: 28px;
    width: 0;
    height: 0;
    opacity: 1; }

  100% {
    top: -1px;
    left: -1px;
    width: 58px;
    height: 58px;
    opacity: 0; } }

.share-box {
  margin-top: 1em;
  border-top: 1px solid #cecece;
  padding-top: 0.5em;
  position: relative;
  text-align: center;

  &::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 1px;
    background-color: #bbb;
    top: 0;
    left: 0; } }

.share-btns {
  display: flex;
  margin-top: .2em;
  align-items: center;
  justify-content: center;

  .fb-share-button {
    margin-right: .5em; } }

@import 'rwd-large';

.homebox-sub {
  display: none;
  &.active {
    display: block; } }

@import 'home1';
@import 'home2';
@import 'home3';
@import 'home4';
