$cirW: 113px;
$boxW: 40px;
$countdownTime: 10s;
@keyframes countdown {
  from {
    stroke-dashoffset: -($cirW); }
  to {
    stroke-dashoffset: 0; } }

@keyframes blessShine {
  from {
    transform: translate(-50%, -50%) scale(0);
    background-color: rgba(#f95901, .25); }

  to {
    transform: translate(-50%, -50%) scale(1);
    background-color: transparent; } }

.blessing {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(#fff, .1);
  overflow: hidden;
  display: none;

  &__fig {
    text-align: center;

    .img-fluid {
      position: relative;
      z-index: 2; }

    &::before {
      content: '';
      position: absolute;
      width: 100%;
      padding-top: 100%;
      background-color: transparent;
      border-radius: 50%;
      left: 50%;
      top: 50%;
      margin-top: -15%;
      transform: translate(-50%, -50%);
      animation: blessShine 3s ease-out infinite;
      z-index: 3; } }

  .buddhaName {
    font-size: 1.5em; }

  &__countdown {
    position: absolute;
    top: 30vh;
    right: 50%;
    margin-right: -($boxW / 2 + 3);
    width: $boxW;
    height: $boxW;
    transform: rotateY(-180deg) rotateZ(-90deg);
    z-index: 4; }

  &__cir {
    stroke-dasharray: $cirW;
    stroke-dashoffset: 0;
    stroke-linecap: round;
    stroke-width: 3px;
    stroke: rgba(yellow, .8);
    fill: none; }

  &.active {
    display: block; }

  &.active &__cir {
    animation: countdown $countdownTime linear forwards; }

  .alert {
    margin-left: 15px;
    margin-right: 15px; } }
